import React, { Component } from 'react';

import Layout from '../components/Layout';

/*========================================================================
// The main page of the app where core functionality is contained.
========================================================================*/
class About extends Component {

    render() {

        /*========================================================================
        // Initially display enter name field. Once a name has been provided,
        // display chat.
        ========================================================================*/
        return (
            <Layout>
                <main className="container-fluid position-absolute h-100" style={{backgroundColor: '#2A275E'}}>
                <div className="row position-absolute w-100 h-100">
                        <section className="col-md-8 d-flex flex-row flex-wrap align-items-center align-content-center px-5">
                            <div className="px-5 mx-5">
                                <span className="d-block w-100 h1 text-light" style={{marginTop: -50}}>
                                    <h1>Hello</h1>
                                </span>
                            </div>
                        </section>
                    </div>
                    <section className="col-md-8">
                        <h1 style={{color: '#fff'}}>About</h1>
                        <p style={{color: '#fff'}}>Sentimental Chat is a real-time chat web app that includes sentiment analysis with Next.js server-side rendering and a Node/Express based back-end.</p>
                        <p style={{color: '#fff'}}>To view the code, check out the official repository: https://github.com/JonnyBeoulve/Sentimental-Chat</p>
                    </section>
                </main>
            </Layout>
        );
    }
}

export default About;


